'use client';

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { useToast } from "@/hooks/use-toast";
import { Info } from "lucide-react";

export default function SettingsPage() {
  const { toast } = useToast();

  const handleShowGuide = () => {
    localStorage.removeItem('hasVisitedTypeAce');
    toast({
      title: "Welcome Guide Reset",
      description: "The welcome guide will now be shown the next time you visit the practice page.",
    });
    // For immediate feedback, we could also trigger the popup here, but for now,
    // let's stick to showing it on the next visit to the home page.
    window.location.href = '/';
  };

  return (
    <div className="flex flex-col gap-8">
      <div className="text-center">
        <h1 className="font-headline text-4xl font-bold tracking-tight lg:text-5xl">
          Settings
        </h1>
        <p className="mt-2 text-lg text-muted-foreground">
          Configure your Type Ace experience.
        </p>
      </div>

      <div className="flex justify-center">
        <Card className="w-full max-w-2xl">
          <CardHeader>
            <CardTitle className="font-headline">Application Settings</CardTitle>
            <CardDescription>
              Manage application settings and preferences.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
             <div className="flex items-center justify-between rounded-lg border p-4">
                <div>
                    <h3 className="font-medium">Show Welcome Guide</h3>
                    <p className="text-sm text-muted-foreground">View the initial startup guide again.</p>
                </div>
                <Button onClick={handleShowGuide} variant="outline">
                    <Info className="mr-2 h-4 w-4" />
                    Show Guide
                 </Button>
             </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
